.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}
.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-primary),
        transparent
    );
    display: grid;
    place-items: center;
}
.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover{
    transform: rotate(0);
}
.about__cards{
    display: grid;
    grid-template-columns: repeat(1,1fr);

}
.about__card{
    background: transparent(0, 0, 0);
    border: 1px solid transparent;
    border-radius: 2rem;
    padding: 2rem;
    text-align: center;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}
.about__card:hover{
    background: black;
    border-color: var(--color-primary-variant);
    cursor: default;
    transform: rotate(0);
}

@media screen and (max-width:1024px){
   
    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p{
        margin: 1rem 0 1.5rem;
    }
}

@media screen and (max-width:600px){
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
        
    }

    .about__cards{
        grid-template-columns: 1fr;
        gap: 1rem;
    }
    .about__content h3{
        text-align: center;
        margin: 1rem 0 1.5rem;
        font-size: smaller;
    }
    
}

